body {
  color: #333;
  background-color: #fafafa;
  margin: 0;
  padding: 0;
  font-family: Roboto, Arial, sans-serif;
}

#app {
  background: #fff;
  border: 1px solid #f0f0f0;
  border-radius: 10px;
  max-width: 600px;
  margin: 40px auto;
  padding: 20px;
  box-shadow: 0 4px 8px #0000001a;
}

h1, h2 {
  text-align: center;
  color: #ff69b4;
  margin-bottom: 20px;
}

input, button {
  box-sizing: border-box;
  border: 1px solid #ced4da;
  border-radius: 25px;
  width: calc(100% - 40px);
  margin: 10px auto;
  padding: 12px;
  font-size: 16px;
  display: block;
}

input:focus, button:focus {
  border-color: #ff69b4;
  outline: none;
}

button {
  color: #fff;
  background-color: #ff69b4;
  border: none;
  transition: background-color .3s, transform .2s;
}

button:hover {
  background-color: #ff85c1;
}

img {
  max-width: 100%;
  margin: 0 auto;
  display: block;
  box-shadow: 0 4px 8px #0000001a;
}

ul {
  padding: 0;
  list-style-type: none;
}

li {
  background: #fff0f5;
  border-radius: 10px;
  margin: 10px 0;
  padding: 15px;
  transition: background .3s;
  box-shadow: 0 2px 4px #0000000d;
}

li:hover {
  background: #ffe4e9;
}

label {
  text-align: center;
  margin-top: 10px;
  display: block;
}

input[type="radio"] {
  display: none;
}

input[type="radio"] + label img {
  cursor: pointer;
  transition: transform .2s, border .2s;
}

input[type="radio"]:checked + label img {
  border: 2px solid #ff69b4;
  transform: scale(1.05);
}

.character-selection {
  justify-content: space-around;
  align-items: center;
  margin-bottom: 20px;
  display: flex;
}

@media (width <= 600px) {
  #app {
    margin: 20px;
    padding: 15px;
  }

  input, button {
    width: calc(100% - 30px);
  }

  .character-selection {
    flex-direction: column;
  }
}
